@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}
body {
  font-family: 'Space Mono', monospace;
  background: hsl(185, 41%, 84%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow: hidden;
  margin: 0;
}

@media (max-width: 376px) {
  body {
    overflow-y: auto;
  }
}

.NumberInput {
  display: inline-block;
  width: 350px;
  height: 100px;
}
.NumberInput h6 {
  font-size: 14px;
  margin: 5px;
  font-weight: 700;
  color: hsl(183, 100%, 15%);
  display: block;
  width: 350px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: left;
  padding-left: 15px;
}

.NumberInput img {
  position: relative;
  left: 25px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.NumberInput input {
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  text-align: right;
  border: solid hsl(189, 41%, 97%);
  border-radius: 5px;
  background-color: hsl(189, 41%, 97%);
  color: hsl(183, 100%, 15%);
  padding: 0 15px;
}
.NumberInput input:hover {
  border: solid hsl(172, 67%, 45%);
  outline: none;
}
.NumberInput input::-webkit-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.NumberInput input:-ms-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.NumberInput input::placeholder {
  color: hsl(184, 14%, 56%);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}

.TipAmount button {
  background-color: hsl(183, 100%, 15%);
  border-radius: 5px;
  border: none;
  color: hsl(0, 0%, 100%);
  cursor: pointer;
  font-family: inherit;
  font-size: 24px;
  font-weight: 400;
  width: 100px;
  height: 40px;
  margin: 5px 0;
}
.TipAmount button:hover {
  background-color: hsl(173, 61%, 77%);
  color: hsl(183, 100%, 15%);
}

@media (max-width: 376px) {
  .TipAmount button {
    width: 140px;
    height: 45px;
  }
}

.TipCustom button {
  background-color: hsl(189, 41%, 97%);
  border-radius: 5px;
  border: none;
  color: hsl(183, 100%, 15%);
  cursor: pointer;
  font-family: inherit;
  font-size: 24px;
  font-weight: 400;
  width: 100px;
  height: 40px;
}
.TipCustom button:hover {
  background-color: hsl(173, 61%, 77%);
  color: hsl(183, 100%, 15%);
  margin: 5px 0;
}

.TipCustom input {
  width: 100px;
  height: 40px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  text-align: right;
  border: solid hsl(189, 41%, 97%);
  border-radius: 5px;
  background-color: hsl(189, 41%, 97%);
  color: hsl(183, 100%, 15%);

  padding: 0 15px;
}
.TipCustom input:hover {
  border: solid hsl(172, 67%, 45%);
  outline: none;
}
.TipCustom input::-webkit-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.TipCustom input:-ms-input-placeholder {
  color: hsl(184, 14%, 56%);
}
.TipCustom input::placeholder {
  color: hsl(184, 14%, 56%);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type='number'] {
  -moz-appearance: textfield;
}

@media (max-width: 376px) {
  .TipCustom button,
  .TipCustom input {
    width: 140px;
    height: 45px;
  }
}

.TipInput {
  width: 350px;
}
.TipInput h2 {
  font-size: 14px;
  font-weight: 700;
  color: hsl(183, 100%, 15%);
  margin-bottom: 5px;
  width: 350px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: left;
  padding-left: 20px;
}
.TipInput-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
  align-content: center;
  justify-content: space-evenly;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-logo {
  pointer-events: none;
  margin: 20px;
}
.App-card {
  background-color: hsl(0, 0%, 100%);
  padding: 20px 0px;
  border-radius: 25px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 850px;
  margin: auto;
}
.App-input {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  /* background-color: rgb(158, 60, 60); */
}
.App-output {
  background-color: hsl(183, 100%, 15%);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  text-align: center;
  align-content: center;
  justify-content: space-evenly;
  margin: 0px;
  padding: 0px 0px;
  border-radius: 20px;
  width: 400px;
  height: 400px;
}
.attribution {
  margin-top: 30px;
}

@media (max-width: 376px) {
  .App {
    position: relative;
    top: 25px;
  }
  .App-card {
    flex-direction: column;
    width: 100vw;
    height: 100%;
  }
  .App-output {
    width: 328px;
    height: 255px;
    justify-content: space-evenly;
  }
  .attribution {
    margin-top: 10px;
  }
}

.PerPerson {
  width: 300px;
  display: flex;
  flex-direction: row;
  text-align: left;
  justify-content: space-between;
  align-items: center;
}
.PerPerson-title-container {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.PerPerson-title {
  font-weight: 700;
  color: hsl(0, 0%, 100%);
  margin: 0;
}
.PerPerson-text {
  color: hsl(185, 41%, 84%);
  font-size: 14px;
  font-weight: 700;
  margin: 0;
}
.PerPerson-output {
  color: hsl(172, 67%, 45%);
  font-weight: 700;
  font-size: 36px;
  margin: 0;
}

.ResetButton {
  background-color: hsl(172, 67%, 45%);
  border-radius: 5px;
  border: none;
  color: hsl(183, 100%, 15%);
  cursor: pointer;
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  width: 300px;
  height: 40px;
  margin: 5px 0;
}
.ResetButton:hover {
  background-color: hsl(173, 61%, 77%);
  color: hsl(183, 100%, 15%);
}
.ResetButton:disabled {
  background-color: hsl(183, 79%, 24%);
}

